import React from 'react';
import PropTypes from 'prop-types';
import './style.css'


/*
<span className="badge bg-l10-info text-info shadow-sm rounded">SIMULAÇÃO</span>
*/
const Header = ({titulo, children,localidade}) => {
	
	return (
		<header >
			<p>
				<strong>{titulo} |</strong> {localidade}
			</p>
            <div className='options'>
            {children}
            </div>
		</header>
	);
};

Header.propTypes = {
    titulo: 		PropTypes.string.isRequired,
    localidade: 	PropTypes.string.isRequired,
    children: 		PropTypes.node.isRequired
};

export default Header;