import { React, useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import PageWrapper from '../../../../layout/PageWrapper/PageWrapper';
import Page from '../../../../layout/Page/Page';
import Button, { ButtonGroup } from '../../../../components/bootstrap/Button';
import Card from '../../../../components/bootstrap/Card';
import { cirurgicalFlow } from '../../../../menu';
import Header from '../../../../components/Header';
import { DropdownItem, DropdownMenu, DropdownToggle } from '../../../../components/bootstrap/Dropdown';
import Dropdown from '../../../../components/bootstrap/Dropdown';
import Modal, { ModalBody, ModalFooter, ModalHeader, ModalTitle } from '../../../../components/bootstrap/Modal';
import API from '../../../../Services'
import "./style.css"




const PreparoLimpeza = () => {

	const navigate = useNavigate()
	const session = JSON.parse(localStorage.getItem('CAPP-local'))
	const localidadeHeader = `Sala ${ session.SUBLOCALIDADE === 'Hemodinamica' ? session.SUBLOCALIDADE : session.SUBLOCALIDADE.split(' ')[1]}`;


	const [perguntas, setPerguntas] = useState([])
	// eslint-disable-next-line no-unused-vars
	const [respostas, setRespostas] = useState([])
	const [inputInicioPreparo, setInputInicioPreparo] = useState('')
	const [inputFimPreparo, setInputFimPreparo] = useState('')



	async function identificaLocalidade() {

		try {
			const consultaEtapaPaciente    = await API.get(`/retornaEtapaPaciente/${session.CODIGO_SALA_CIRURGICA}`)
			const localidade 			   = consultaEtapaPaciente.data[0];
			const consultaInicioFimPreparo = await API.get(`/consultar-assinatura-preparo/${session.CODIGO_SALA_CIRURGICA}`);
		
			if(localidade.ETAPA === "LIMPEZA"){
					navigate(`/salaCirurgica/limpeza`, {
						state: {
							MOTIVO_ALTA: localidade.CD_ETAPA,
							AVISO: 		 localidade.CD_AVISO_CIRURGIA,
							SAIDA_SALA:  false
						}
					}) 
				}

				if (consultaInicioFimPreparo.data.INICIO_PREPARO != null && consultaInicioFimPreparo.data.FIM_PREPARO != null) 
				{
					if(localidade.ETAPA === "SALA CIRURGICA"){
							navigate('/salaCirurgica/acoes')
					}else{
							navigate('/salaCirurgica/recebimento')
					}
						
				}else{
					await retornaPerguntasChecklist()
					await retornaRespostas()
				}
				
		} catch (error) {
			console.log(error)
		}
	}


	useEffect(() => {
		
		identificaLocalidade()
		
	}, [])



/*
	const verificaQuadro = async () => {
		const request = await API.get(`/retornaPacienteQuadroAcoes/${session.CODIGO_SALA_CIRURGICA}`)
		const response = request.data;
		if ((response).length > 0) {
			//alert('Há um paciente no quadro de ações')
			// CRIAR UM STORAGE COM O USUÁRIO RESPONSÁVEL

			/*const buscaAssinante  = await API.get(`/retornaAssinanteAcoes/${response[0].COD_AVISO}`)
			const responseBuscaAssinante = buscaAssinante.data[0];
				localStorage.setItem('responsavel_recebimento', JSON.stringify({
					USUARIO: responseBuscaAssinante.CD_USUARIO_OPERACAO
				}));
				
			*/
			//navigate('/salaCirurgica/acoes')
		//}
	//};



	const alterarLocalidade = () => {
		localStorage.removeItem('CAPP-local');
		localStorage.removeItem('0')
		navigate('/')
	};



	// retorna pergunta (Início e Fim)
	const retornaPerguntasChecklist = async () => {
		try {
			const requestPerguntas = await API.get(`/pergunta-sem-aviso/50`);
			setPerguntas(requestPerguntas.data)
		} catch (error) {
			console.log(error);
		}
	}


	const retornaRespostas = async () => {
		try {
			const requestRespostas = await API.get(`/consultar-assinatura-preparo/${session.CODIGO_SALA_CIRURGICA}`);

			
			/*
			if (requestRespostas.data.INICIO_PREPARO != null && requestRespostas.data.FIM_PREPARO != null) {

				navigate('/salaCirurgica/acoes')
				return;
			}
		*/

			setRespostas(requestRespostas.data)

			// Se a data seja de (início ou fim) já tiver sido inserida, bloqueia os botões.
			if (requestRespostas.data.INICIO_PREPARO != null) {
				document.getElementById('14').setAttribute("disabled", "disabled");
			}
			if (requestRespostas.data.FIM_PREPARO != null) {
				document.getElementById('15').setAttribute("disabled", "disabled");
			}


		} catch (error) {
			console.log(error);
		}
	}


	// Assinar datas
	const assinar = async (codigoPergunta) => {

		/**
		 * NOTA: se clicar e n tiver data no banco, registrar a data atual e redirecionar o usuário para assinatura, seja inicio ou fim
		 */

		if (codigoPergunta === 14) {
			// É uma data retroativa? 
			if ((inputInicioPreparo).length > 0) {
				navigate(`/salaCirurgica/Assinatura`, {
					state: {
						CODIGO_LOCALIDADE: session.CODIGO_LOCALIDADE,
						CODIGO_SUB_LOCALIDADE: session.CODIGO_SUBLOCALIDADE,
						CODIGO_SALA_CIRURGICA: session.CODIGO_SALA_CIRURGICA,
						CODIGO_PERGUNTA: codigoPergunta,
						RESPOSTA_PERGUNTA: inputInicioPreparo
					}
				})
				return;
			}
			// Já tem alguma data de início inserida no banco? 
			if (!(respostas.INICIO_PREPARO)) {
				// Então registra o momento exato.
				const momentoAtual = new Date();
				momentoAtual.setMinutes(momentoAtual.getMinutes() - momentoAtual.getTimezoneOffset());
				document.getElementById('data-inicio').value = momentoAtual.toISOString().slice(0, 16);
				const dataFormatada = momentoAtual.toISOString().slice(0, 16);

				navigate(`/salaCirurgica/Assinatura`, {
					state: {
						CODIGO_LOCALIDADE: session.CODIGO_LOCALIDADE,
						CODIGO_SUB_LOCALIDADE: session.CODIGO_SUBLOCALIDADE,
						CODIGO_SALA_CIRURGICA: session.CODIGO_SALA_CIRURGICA,
						CODIGO_PERGUNTA: codigoPergunta,
						RESPOSTA_PERGUNTA: dataFormatada
					}
				})
				return;
			}
		}


		if (codigoPergunta === 15) {
			// É uma data retroativa? 
			if ((inputFimPreparo).length > 0) {

				// A data de fim é menor que a data de início? (Validação )
				if (new Date(inputFimPreparo).getTime() < new Date(respostas.INICIO_PREPARO).getTime()) {
					//alert('A data de fim não pode ser menor que a data de início')
					setModalValidacao(true)
					return;
				}

			}

			// Quer finalizar? mas já informou a data de início?
			if (!(respostas.INICIO_PREPARO)) {
				//alert('É necessário informar a data de início antes da fim.')
				setModalAlerta(true)
				document.getElementById('14').focus();

				return;
			}
			// Já tem alguma data de fim inserida no banco? 
			if (!(respostas.FIM_PREPARO)) {
				// Então registra o momento exato.
				const momentoAtual = new Date();
				momentoAtual.setMinutes(momentoAtual.getMinutes() - momentoAtual.getTimezoneOffset());
				document.getElementById('data-inicio').value = momentoAtual.toISOString().slice(0, 16);
				const dataFormatada = momentoAtual.toISOString().slice(0, 16);

				navigate(`/salaCirurgica/Assinatura`, {
					state: {
						CODIGO_LOCALIDADE: session.CODIGO_LOCALIDADE,
						CODIGO_SUB_LOCALIDADE: session.CODIGO_SUBLOCALIDADE,
						CODIGO_SALA_CIRURGICA: session.CODIGO_SALA_CIRURGICA,
						CODIGO_PERGUNTA: codigoPergunta,
						RESPOSTA_PERGUNTA: dataFormatada
					}
				})

			}
		}
		// eslint-disable-next-line consistent-return
		return true;
	}


	function formataData(d) {
		const dataHora = d;
		const data = new Date(dataHora);
		const dia = data.getDate().toString().padStart(2, '0');
		const mes = (data.getMonth() + 1).toString().padStart(2, '0');
		const ano = data.getFullYear().toString();
		const hora = data.getHours().toString().padStart(2, '0');
		const minuto = data.getMinutes().toString().padStart(2, '0');

		const dataHoraFormatada = `${dia}/${mes}/${ano} às ${hora}:${minuto}h`;
		return dataHoraFormatada;
	}

	// eslint-disable-next-line no-unused-vars
	const [modalConfirmData, setmodalConfirmData] = useState(false)
	// eslint-disable-next-line no-unused-vars
	const [modalAlerta, setModalAlerta] = useState(false)
	// eslint-disable-next-line no-unused-vars
	const [modalValidacao, setModalValidacao] = useState(false)
	// eslint-disable-next-line no-unused-vars
	const [auxCodPergu, setAuxCodPergu] = useState(null)

	function confirmacaoModalData(codigoPergunta) {
		setAuxCodPergu(codigoPergunta)
		// início do preparo da sala?
		if (codigoPergunta === 14) {
			if (inputInicioPreparo.length > 0)	// Houve uma interação no campo de início do preparo da sala?
			{
				// existe alguma data salva no banco?
				if ((respostas.INICIO_PREPARO)) {	// sim? então a ação atual é uma alteração de valor.
					// são as mesmas datas?
					if ((new Date(respostas.INICIO_PREPARO).getTime()) === (new Date(inputInicioPreparo.replace(/T/g, ' ')).getTime())) {
						return false;
					}
					
					document.getElementById("14").click();
					setmodalConfirmData(true)
					return true;
				}
				// Se informou uma data retroativa manualmente, abre a modal e redireciona pra assinatura.
				setmodalConfirmData(true)
				return true;

			}
		}

		else if (codigoPergunta === 15) {
			if (inputFimPreparo.length > 0) {
				if ((respostas.FIM_PREPARO)) {
					if ((new Date(respostas.FIM_PREPARO).getTime()) === (new Date(inputFimPreparo.replace(/T/g, ' ')).getTime())) {
						return false;
					}


					document.getElementById("15").click();
					setmodalConfirmData(true)
					return true;
				}

				// Se informou uma data retroativa manualmente, abre a modal e redireciona pra assinatura.
				if (!(respostas.INICIO_PREPARO)) {		/** Se não informou o início, não pode informar o fim. */
					setModalAlerta(true)
					return false;
				}

				// A data de fim é menor que a data de início? (Validação )
				if (new Date(inputFimPreparo).getTime() < new Date(respostas.INICIO_PREPARO).getTime()) {
					//alert('A data de fim não pode ser menor que a data de início')
					setModalValidacao(true)
					return false;
				}

				setmodalConfirmData(true)
				return true;

			}
		}
		return true;
	}


	return (

		<PageWrapper title={cirurgicalFlow.SALA_CIRURGICA_LIMPEZA.text}>
			<Header titulo={localidadeHeader} localidade='Preparo e Limpeza'>
				<Dropdown >
					<DropdownToggle>
						<Button
							color="light"
							icon="menu"
							isLight
						>
							<p> </p>
						</Button>
					</DropdownToggle>
					<DropdownMenu>
						<DropdownItem onClick={() => alterarLocalidade()}>
							Alterar Local
						</DropdownItem>
					</DropdownMenu>
				</Dropdown>
			</Header>
			<Page>
				<div className='row'>
					<div className="container" style={{ marginTop: '150px' }}>

					{
						perguntas.length > 0 ?
								
						<Card borderSize="3" borderColor="success" style={{
							paddingLeft: '9px',
							margin: 'auto'
						}}>

							
							<div className='row' >
								<div className='col-sm-4 pull-right' style={{ backgroundColor: '#C7EBE7', padding: '12px', borderRadius: '20px 0 0 20px', borderColor: '#37BBAA', borderSize: '2px' }}>
									<div style={{ padding: '70px 0', textAlign: 'center', margin: 'auto' }}>
										<h1>Preparo</h1>
									</div>
								</div>
								<div className='col-sm-8' style={{ padding: '12px' }}>
									<div className='row'>
										{
											// eslint-disable-next-line no-unused-vars
											perguntas.map((q, i) => (
												<div key={q.CD_PERGUNTA} className='col-sm-6 ' style={{ margin: 'auto', textAlign: 'center' }}>
													<div className="row ">
														<div className="col-sm-12 pull-right">
															<Button id={q.CD_PERGUNTA} onClick={() => assinar(q.CD_PERGUNTA)} style={{ fontWeight: '700', fontSize: '21px', height: '129px', borderRadius: '37px' }} color="success" size="large">{q.DS_PERGUNTA}</Button>
														</div>
														<div className="col-sm-12" style={{ marginTop: '10px' }}>
															<div >
																{
																	// Se for a primeira pergunta, mostrará o primeiro campo do tipo time
																	i === 0 ?
																		<input onBlur={() => confirmacaoModalData(q.CD_PERGUNTA)} style={{ width: '190px', height: '50px', borderRadius: '20px', backgroundColor: '#37BBAA', textAlign: 'center', margin: 'auto', paddingTop: '1px', paddingLeft: '15px', fontWeight: 'bold', fontSize: '18px', color: 'white', border: '0px' }} className='input-time' onChange={(e) => setInputInicioPreparo(e.target.value)} type="datetime-local" id="data-inicio" name="date" defaultValue={respostas.INICIO_PREPARO} />
																		// se for o segundo, mostrará o segundo campo.
																		:
																		<input onBlur={() => confirmacaoModalData(q.CD_PERGUNTA)} style={{ width: '190px', height: '50px', borderRadius: '20px', backgroundColor: '#37BBAA', textAlign: 'center', margin: 'auto', paddingTop: '1px', paddingLeft: '15px', fontWeight: 'bold', fontSize: '18px', color: 'white', border: '0px' }} className='input-time' onChange={(e) => setInputFimPreparo(e.target.value)} type="datetime-local" id="data-fim" name="date" defaultValue={respostas.FIM_PREPARO} />
																}

															</div>
														</div>
														<div className="col-sm-12" style={{ marginTop: '10px' }}>
															<div style={{ "fontSize": "17px", "fontWeight": "bold" }} >
																{i === 0 ?
																	respostas.CD_USUARIO_INICIO === null ?
																		<span style={{ "color": "transparent" }}>PENDENTE</span>
																		:
																		respostas.CD_USUARIO_INICIO
																	:
																	respostas.CD_USUARIO_FIM === null ?
																		<span style={{ "color": "transparent" }}>PENDENTE</span>
																		:
																		respostas.CD_USUARIO_FIM
																}
															</div>
														</div>
													</div>
												</div>
											))
										}
									</div>
								</div>
							</div>
						</Card>
						:
							<div className="lds-ring"><div /><div /><div /><div /></div>
						}
					</div>
				</div>

				<Modal
					id='confirm'
					isAnimation={false}
					isCentered
					isOpen={modalConfirmData}
					titleId='confirm'>
					<ModalHeader className='modal-confir-data-warning'>
						<ModalTitle>CONFIRMAÇÃO</ModalTitle>
					</ModalHeader>
					<ModalBody className='ModalBody'>
						<br />
							<h3>Você confirma a alteração da data de {auxCodPergu === 14 ? 'início' : 'fim'} para:  <strong>{auxCodPergu === 14 ? formataData(inputInicioPreparo) : formataData(inputFimPreparo)}</strong>?</h3>
						<br />
					</ModalBody>
					<ModalFooter>
						<ButtonGroup isToolbar="true" size='lg'>
							<Button color='success' onClick={() => {
								setmodalConfirmData(false)
								navigate(`/salaCirurgica/Assinatura`, {
									state: {
										CODIGO_LOCALIDADE: session.CODIGO_LOCALIDADE,
										CODIGO_SUB_LOCALIDADE: session.CODIGO_SUBLOCALIDADE,
										CODIGO_SALA_CIRURGICA: session.CODIGO_SALA_CIRURGICA,
										CODIGO_PERGUNTA: auxCodPergu,
										RESPOSTA_PERGUNTA: auxCodPergu === 14 ? inputInicioPreparo : inputFimPreparo
									}
								})
							}}>
								CONFIRMAR
							</Button>
							<Button color='light' onClick={() => setmodalConfirmData(false)}>
								CANCELAR
							</Button>
						</ButtonGroup>
					</ModalFooter>
				</Modal>

				<Modal
					id='alert'
					isCentered
					isAnimation={false}
					isOpen={modalAlerta}
					titleId='alert'>
					<ModalHeader className='modal-confir-data-error'>
						<ModalTitle>ALERTA</ModalTitle>
					</ModalHeader>
					<ModalBody className='ModalBody'>
						<h3>Você precisa informar a data de início antes.</h3>
					</ModalBody>
					<ModalFooter>
						<ButtonGroup isToolbar="true" size='lg'>
							<Button color='light' onClick={() => setModalAlerta(false)}>
								FECHAR
							</Button>
						</ButtonGroup>
					</ModalFooter>
				</Modal>


				<Modal
					id='validacao'
					isCentered
					isAnimation={false}
					isOpen={modalValidacao}
					titleId='validacao'>
					<ModalHeader className='modal-confir-data-error'>
						<ModalTitle>ALERTA</ModalTitle>
					</ModalHeader>
					<ModalBody className='ModalBody'>
						<h3>A data fim não pode ser menor que a data de início!</h3>
					</ModalBody>
					<ModalFooter>
						<ButtonGroup isToolbar="true" size='lg'>
							<Button color='light' onClick={() => setModalValidacao(false)}>
								FECHAR
							</Button>
						</ButtonGroup>
					</ModalFooter>
				</Modal>

			</Page>
		</PageWrapper>


	);


};

export default PreparoLimpeza;